.add-image-slim-wrapper {
    background: white;
    border: 0.05rem solid #353535;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 12rem;
    position: absolute;
}

.add-image-slim-container {
    height: 250px;
    width: 250px;
    margin: 0 auto;
}

.add-image-title-bar {
    background: -webkit-gradient(linear, left top, left bottom, from(#f8f8f8), to(#d6d6d6));
    background: -webkit-linear-gradient(#f8f8f8, #d6d6d6);
    background: -o-linear-gradient(#f8f8f8, #d6d6d6);
    background: linear-gradient(#f8f8f8, #d6d6d6);
    border-bottom: 0.02rem solid #585858;
    padding-top: 0.05rem;
    padding-bottom: 0.25rem;
    width: 100%;
}

.add-image-title {
    margin: 0;
}

.add-image-button-container {
    background: -webkit-gradient(linear, left top, left bottom, from(#f1f1f1), to(#a3a3a3));
    background: -webkit-linear-gradient(#f1f1f1, #a3a3a3);
    background: -o-linear-gradient(#f1f1f1, #a3a3a3);
    background: linear-gradient(#f1f1f1, #a3a3a3);
    width: 100%;
}

.add-image-done {
    border: 0.05rem solid #565656;
    margin-top: 0.5rem;
    margin-bottom: 0.25rem;
    font-size: 1rem;
    height: 1.7rem;
}


